.body-report {
  overflow-y: auto;
  background: url(../images/bg-report.png) no-repeat;
  background-size: cover;
  height: 100vh;
  .contenedor-search {
    background-size: cover;
    padding: 100px 1rem 0 1rem;
    border-radius: 0 0 35px 35px;
    @media (min-width: 525px) {
      padding: 150px 1rem 0 1rem;
    }
    .search {
      display: flex;
      align-items: center;
      position: relative;
      .icon-search {
        position: absolute;
        right: 0;
        transform: translateX(-140%);
        width: 6%;
        @media (min-width: 525px) {
          width: 3%;
        }
      }
      .form-control {
        background: #fff;
        line-height: 2;
        border-color: transparent;
        border-radius: .7rem;
        font-size: .8rem;
        // margin-right: .7rem;
        padding: .6rem .8rem;
        &:focus {
          background: #fff;
          border-color: transparent;
          box-shadow: 0 0 0 2px rgb(194, 206, 202);
          color: #EB8950;
        }
      }
    }
  }
  .contenedor {
    padding-bottom: 70px;
    .card {
      border-radius: 15px;
      border: none;
      margin: 0 .3rem;
      img {
        border-top-left-radius: 15px;
        border-top-right-radius: 15px;
      }
      .card-body {
        flex: 1 1 auto;
        padding: .5rem;
        .etiqueta {
          background-color: #ffd6a9;
          width: 37%;
          color: #fe5a3fff;
          padding: 0 .5rem;
          text-align: center;
          border-radius: 15px;
          font-size: .7rem;
          margin-bottom: .5rem;
        }
        .autor-card {
          display: flex;
          align-items: center;
          img {
            width: 18%;
          }
          p {
            margin-bottom: 0;
            color: #fe5a3fff;
            font-weight: 600;
            margin-bottom: 0;
          }
        }
        .card-text {
          font-size: .8rem;
        }
        .view-more {
          text-align: end;
          a {
            text-decoration: none;
            font-size: .8rem;
            color: #2fd9acff;
          }
        }
      }
    }
  }
}